@import url("./main.css");

h2 {
  font-size: 48px;
  margin: 0;
}

.left {
  grid-row: span 3;
  grid-column: 1/3;
  color: var(--primary-color);
  text-align: center;
}

.right {
  grid-row: auto;
  grid-column: 3/9;
  font-size: 25px;
  text-align: center;
}

.en-title {
  font-weight: 100;
}

.ko-title {
  font-weight: 800;
}

.artist-section {
  grid-column: auto/span 3;
  border: 0.5px solid black;
  text-align: center;
  padding: 25px;
}

.artist-name {
  color: black;
  font-weight: 900;
  font-size: 24px;
  display: inline-block;
}

.artist-position {
  display: inline-block;
  font-size: 20px;
  font-weight: normal;
  color: #797777;
  margin-left: 10px;
}

.artist-photo {
  border-radius: 50%;
  width: 180px;
  height: 180px;
  margin: auto;
}

