@import url('./main.css');

.gallery {
    grid-row: 1;
    grid-column: 1/9;
}

.section {
    background-color: #E7e7e7;
    border: 3px solid var(--primary-color);
    padding: 20px;
}

.section1 {
    grid-column: 1/ 5;
}

.section2 {
    grid-column: 5/9;
    grid-row: 2/5;
}

@media (max-width: 768px) {

    .gallery {
        display: block;
    }

    .section {
        display: block;
        margin-top: 20px;
    }
}